














































import Vue from 'vue';

export default Vue.extend({


    data: () => ({
        fav: true,
        menu: false,
        message: false,
        hints: true,
        count: 1,

        snackbar: false,
        text: "Article ajoute a la commande"
    }),

    props: ['item'],

    methods: {
        onAddItem() {
            this.$emit('onAddItem', {
                item: this.item,
                count: this.count,
            });

            this.count = 1;
            this.menu = false;

            this.snackbar = true;
        }
    }


});
